import React from "react";
import Image from "material-ui-image";
import { Subtitle, Text, Title } from "./Typography";
import { PUBLIC_URL } from "../lib/constants";

export const RoadmapSection = ({ title, subtitle, text, image, reverse = false }) => {
  const content = [
    <div style={{ flexShrink: 1.2 }} key="content">
      <div>
        <Subtitle>{subtitle}</Subtitle>
      </div>
      <div style={{ margin: "30px 0" }}>
        <Title>{title}</Title>
      </div>
      <div>
        <Text>{text}</Text>
      </div>
    </div>,
    <div style={{ width: "100%" }} key="image">
      {image}
    </div>,
  ];

  return (
    <div style={{ padding: "100px 40px", display: "flex", gap: 40, color: "white", alignItems: "center" }}>
      {reverse ? content.reverse() : content}
    </div>
  );
};
